* {
    margin: 0;
    padding: 0;
}

html {
    font-size: 62.5%;
}

@media(max-width:990px) {
    html {
        font-size: 45%;
    }
}

@media(max-width:550px) {
    html {
        font-size: 32%;
    }
}

body {
    font-size: 1.6rem;
}

.container {
    width: 100%;
    min-height: 100vh;
    display: grid;
    place-items: center;
}

a {
    text-decoration: none;
    color: #fff;

}

a,
button {
    font-size: 2rem;
}